import React from "react";

/* ============================================================
 üß† GOOGLE SHEETS API ‚Äî TypeScript SDK (Full Version)
 ============================================================ */

const BASE_URL =
  "https://script.google.com/macros/s/AKfycbzdWXbM8Q_hBnKnk5LXj20A2CLmxBxI0uro5eNl2Yo7i9ShHrTBIJQy6Scz7Wu5g-xu8A/exec";
const PASSWORD = "hT_W8.pN-kQv7rZ~sD1y";

export interface SheetRow {
  [key: string]: string;
}

export interface PaginationResponse {
  mode: "pagination";
  page: number;
  limit: number;
  totalPages: number;
  totalCount: number;
  hasNext: boolean;
  hasPrev: boolean;
  next: string | null;
  prev: string | null;
  rows: SheetRow[];
}

export interface CountResponse {
  mode: "count";
  totalCount?: number;
  afterCount?: number;
  note?: string;
}

export interface DeleteResponse {
  mode: "delete";
  status: "deleted" | "not_found";
  row?: number;
  deleted?: SheetRow;
}

export type DefaultResponse = SheetRow[];
export type ApiResponse =
  | PaginationResponse
  | CountResponse
  | DeleteResponse
  | DefaultResponse;

/* ============================================================
   Internal helper
   ============================================================ */

async function fetchJson<T>(params: Record<string, string | number>): Promise<T> {
  const query = new URLSearchParams({
    password: PASSWORD,
    ...Object.fromEntries(Object.entries(params).map(([k, v]) => [k, String(v)])),
  });
  const res = await fetch(`${BASE_URL}?${query.toString()}`);
  if (!res.ok) throw new Error(`HTTP ${res.status} - ${res.statusText}`);
  return (await res.json()) as T;
}

/* ============================================================
   API Functions
   ============================================================ */

async function getNextRows(timestamp: string): Promise<DefaultResponse> {
  return await fetchJson<DefaultResponse>({ timestamp });
}

async function countRows(timestamp?: string): Promise<CountResponse> {
  const params: Record<string, string> = { mode: "count" };
  if (timestamp) params.timestamp = timestamp;
  return await fetchJson<CountResponse>(params);
}

async function deleteRow(idOrTimestamp: string, type: "id" | "timestamp" = "id"): Promise<DeleteResponse> {
  return await fetchJson<DeleteResponse>({
    mode: "delete",
    [type]: idOrTimestamp,
  });
}

async function deleteById(id: string): Promise<DeleteResponse> {
  return await deleteRow(id, "id");
}

async function deleteByTimestamp(timestamp: string): Promise<DeleteResponse> {
  return await deleteRow(timestamp, "timestamp");
}

async function getPaginatedRows(options: { page?: number; limit?: number; timestamp?: string } = {}): Promise<PaginationResponse> {
  const params: Record<string, string | number> = {
    mode: "pagination",
    page: options.page ?? 1,
    limit: options.limit ?? 10,
  };
  if (options.timestamp) params.timestamp = options.timestamp;
  return await fetchJson<PaginationResponse>(params);
}

